body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background: #000000e6;
}

#root {

}

#app-title {
  color: white;
  grid-column-start: 2;
  grid-column-end: 5;
  margin: auto;
  font-family: 'Patua One', cursive;
  font-size: 50px;
}

.App {
  display: grid;
  grid-template-columns: 200px 300px auto 300px 200px;
  grid-template-rows: 15% 5% auto auto 20%;
}

.MinutesInputForm {
  display: grid;
  grid-template-columns: 10% auto auto auto 10%;
  grid-template-rows: auto;

  background: #fffffffa;
  padding-top: 20px;
  padding-bottom: 26px;
  border-radius: 8px;
  grid-column-start: 2;
  grid-column-end: 5;
  grid-row-start: 2;
  grid-row-end: 4;

  color: black;

  border-style: solid;
  border-color: #1c795661;
  border-width: 43px 0px 2px 0px;
}

.MinutesInputForm h2 {
  padding-bottom: 15px;
  
  grid-column-start: 2;
  grid-column-end: 5;
  margin: auto;
}

.MinutesInputForm #add-times-button {
  grid-column-start: 3;
  width: 42%;
  justify-self: center;
  height: 40px;
  background: #77de608c;
  border-style: none none solid none;
  border-width: 3px;
  border-radius: 7px;
  font-size: 23px;
  outline: none;
  cursor: pointer;
}


.MinutesInput {
  font-size: 26px;
  text-align: center;
  width: 39%;
  min-width: 80px;
  justify-self: center;

  border-style: solid;
  border-color: #1c795661;
  border-width: 7px 3px 3px 3px;
  background: unset;
  border-radius: 7px;

  outline: none;
  color: #000000b8;

  font-family: 'Roboto Mono', monospace;
  
}

.input-container {
  grid-column-start: 3;
  grid-column-end: 4;
  margin-bottom: 10px;

  display: grid;
}


#total-time-number {
  font-family: 'Roboto Mono', monospace;
  text-align: center;
}

#remove-all-times-button {
  grid-column-start: 1;
  grid-row-start: 1;
  padding: 0;
  margin: 0;
  border-style: none;
  background: lightcoral;
  border-radius: 7px;
  width: 77%;
  height: 30px;
  font-size: 16px;
  justify-self: center;
  top: -57px;
  right: -1px;
  position: relative;
  outline: none;
  cursor: pointer;
}
